@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNovaExtrabldIt.eot");
  src: url("../fonts/ProximaNovaExtrabldIt.eot?#iefix") format("embedded-opentype"),
    url("../fonts/ProximaNovaExtrabldIt.woff") format("woff"),
    url("../fonts/ProximaNovaExtrabldIt.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNovaLightIt.eot");
  src: url("../fonts/ProximaNovaLightIt.eot?#iefix") format("embedded-opentype"),
    url("../fonts/ProximaNovaLightIt.woff") format("woff"),
    url("../fonts/ProximaNovaLightIt.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
